function dra (arr) {
  var can = document.getElementById('canvas')
  var ctx = can.getContext('2d')
  can.width = 800
  can.height = 400
  var spa = 10
  var ratio = 20
  var diff = 10
  spa = can.width / arr.length
  if (spa < 15) {
    can.width = 15 * arr.length
    spa = 15
  }
  ratio = can.height / 18
  ctx.font = '14px Georgia'
  //   ctx.textAlign = 'center'
  ctx.textBaseline = 'middle'
  for (var i = 0; i < arr.length; i++) {
    ctx.beginPath()
    ctx.moveTo(spa * i, converNum(arr[i], ratio))
    ctx.lineTo(spa * (i + 1), converNum(arr[i + 1], ratio))
    ctx.stroke()
    ctx.beginPath()
    ctx.fillStyle = '#999'
    if (arr[i] < arr[i + 1]) {
      diff = -diff
    } else {
      diff = Math.abs(diff)
    }
    ctx.fillText(arr[i], spa * i - Math.abs(diff), converNum(arr[i], ratio) - diff)
    ctx.arc(spa * i, converNum(arr[i], ratio), 3, 0, 2 * Math.PI)
    ctx.fill()
  }
}
function converNum (num, a) {
  return 400 - (+num) * a
